import { Currency, NativeCurrency, Token, WETH9 } from '@uniswap/sdk-core'
import invariant from 'tiny-invariant'

export class CommonEther extends NativeCurrency {
  protected constructor(chainId: number) {
    type EthMap = { [chainId: number]: {decimals:number,symbol:string,name:string} }
    const COMMON:EthMap={
      [1]:{decimals:18,symbol:'ETH',name:'Ether'},
      [3]:{decimals:18,symbol:'ETH',name:'Ether'},
      [4]:{decimals:18,symbol:'ETH',name:'Ether'},
      [5]:{decimals:18,symbol:'ETH',name:'Ether'},
      [42]:{decimals:18,symbol:'ETH',name:'Ether'},
      [42161]:{decimals:18,symbol:'ETH',name:'Ether'},
      [421611]:{decimals:18,symbol:'ETH',name:'Ether'},
      [10]:{decimals:18,symbol:'ETH',name:'Ether'},
      [69]:{decimals:18,symbol:'ETH',name:'Ether'},
      [256]:{decimals:18,symbol:'THT',name:'THT'},
      [128]:{decimals:18,symbol:'HT',name:'HT'},
      [97]:{decimals:18,symbol:'TBNB',name:'TBNB'},
      [56]:{decimals:18,symbol:'BNB',name:'BNB'},
      [65]:{decimals:18,symbol:'TOKT',name:'TOKT'},
      [66]:{decimals:18,symbol:'OKT',name:'OKT'},
      [80001]:{decimals:18,symbol:'TMATIC',name:'TMATIC'},
      [137]:{decimals:18,symbol:'MATIC',name:'MATIC'},
    }
    super(chainId,COMMON[chainId].decimals,COMMON[chainId].symbol,COMMON[chainId].name)
  }

  public get wrapped(): Token {
    const weth9 = WETH9[this.chainId]
    invariant(!!weth9, 'WRAPPED')
    return weth9
  }

  private static _etherCache: { [chainId: number]: CommonEther } = {}

  public static onChain(chainId: number): CommonEther {
    return this._etherCache[chainId] ?? (this._etherCache[chainId] = new CommonEther(chainId))
  }

  public equals(other: Currency): boolean {
    return other.isNative && other.chainId === this.chainId
  }
}
